// John
// import RelaCommentBar from '@/components/rela-comment-bar.vue'; //相关点评条

<template>
  <div class="RelaCommentBar" v-if="vData">
    <div class="RelaCommentBar-h1">{{vTitle}}</div>
    <div class="RelaCommentBar-tiG">
      <div class="RelaCommentBar-img" ></div>
      <div class="RelaCommentBar-ti">{{vData.product.title}}</div>
      <!-- 判断图片是否显示 -->
      <div class="RelaCommentBar-te" :style="{height:true?'120px':null,maxHeight:true?'120px':null}">{{vData.content}}</div>
      <div class="RelaCommentBar-iconG">
        <!-- <img  v-if="false" src="@/assets/comment-bar/in_like_border_icon@2x.png" alt=""> -->
        <span class="RelaCommentBar-loveIcon icon-in_like_border_icon"></span>
        <!-- <img v-else src="@/assets/comment-bar/in_like_icon@2x.png" alt=""> -->
        <span class="RelaCommentBar-span">{{vData.likes}}</span>
        <img class="RelaCommentBar-iconComment" src="@/assets/comment-bar/in_comments_border_icon@2x.png" alt="">
        <span class="RelaCommentBar-span">{{vData.reply_count}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props:[
    'vTitle',
    'vData',// 缺
  ],
  data(){
    return {
     
    }
  }
}
</script>

<style scoped lang="less">
  @m:750/375;
  .RelaCommentBar-tiG{
    margin-top:14px*@m;
    padding:20px*@m 14px*@m 14px*@m;
    background: #FFFFFF;
    box-shadow: 0 0 30px*@m 0 rgba(227,227,227,0.60);
    border-radius: 8px*@m
  }
    .RelaCommentBar-h1{
      font-family: PingFangSC-Medium;
      font-size: 14px*@m;
      color: #0F2540;
      letter-spacing: 0;
      line-height: 14px*@m;
    }
    .RelaCommentBar-img{
      float:left;
      margin-top:-9px*@m;
      margin-right:14px*@m;
      border-radius: 8px;
      width:100px*@m;height: 100px*@m;
      background:skyblue;
    }
    .RelaCommentBar-ti{
      font-family: PingFangSC-Medium;
      font-size: 13px*@m;
      color: #0F2540;
      letter-spacing: 0;
      line-height: 18px*@m;
    }
    .RelaCommentBar-te{
      margin-top:10px*@m;
      font-family: PingFangSC-Regular;
      font-size: 13px*@m;
      color: #BCBCBC;
      line-height: 18px*@m;
      overflow: hidden;
    }
    .RelaCommentBar-iconG{
      margin-top:14px*@m;
    }
      .RelaCommentBar-loveIcon{
        opacity: 0.2;
        color: #37394C;
        font-size: 16px*@m;
      }
      .RelaCommentBar-iconComment{
        margin-left:15px*@m;
      }
      .RelaCommentBar-span{
        opacity: 0.5;
        font-family: PingFangSC-Regular;
        font-size: 12px*@m;
        color: #BCBCBC;
        letter-spacing: 1.11px*@m;
        margin-left:8px*@m;
      }
</style>



