import Vue from 'vue';
import App from '@/App';
import Router from 'vue-router'
// import CommentList from '@/view/comment-list.vue'; //点评列表
import HotComment from '@/view/comment/comment-hot-comment.vue'; //热门点评页面

import 'html5-reset/assets/css/reset.css'

Vue.config.productionTip = false
Vue.use(Router)

var router = new Router({
  // mode: 'history',
  // base: '/hot-comment.html',
  routes: [
    {
      path: '/',
      name: 'HotComment',
      component: HotComment
    }
  ]
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


